function Form(){
  const[canSendReq, setCanSendReq] = useState(true);
  function checkUsername(user){
    let isGoodStr = true;
    if(user.toLowerCase() == "admin")
      isGoodStr = false;
    if(user.length > 15)
      isGoodStr = false;
    if(!(/^[a-zA-Z]\w*[a-zA-Z]$/).test(user))
      isGoodStr = false;

    return isGoodStr;
  }
  function checkText(text){
    let isGoodStr = true;
    if((/^\s*$/).test(text))
      isGoodStr = false;
    return isGoodStr;
  }
  useEffect(() => {
    document.querySelector("#addTweetName").addEventListener("input", (e) => {
      checkUsername(e.target.value) ? e.target.classList.remove("badStr") : e.target.classList.add("badStr");
    });
    document.querySelector("#addTweetText").addEventListener("input", (e) => {
      checkText(e.target.value) ? e.target.classList.remove("badStr") : e.target.classList.add("badStr");
    });
  }, []);

  function addTweet(){
    if(!checkUsername(document.querySelector("#addTweetName").value) || !checkText(document.querySelector("#addTweetText").value))
      return;
    document.querySelector("#addTweetName").classList.remove("badStr");
    document.querySelector("#postBtn").disabled = true;
    document.querySelector("#mainPostLoader").classList.remove("invLoader");
    const addToDb = async () => {
      try{
        await addDoc(collection(db, "tweets"), {
          user:document.querySelector("#addTweetName").value.trim(),
          text:document.querySelector("#addTweetText").value.trim(),
          replies:[],
          date:new Date(),
        });
        document.querySelector("#addTweetName").value = "";
        document.querySelector("#addTweetText").value = "";
      }
      catch(err){
        console.log(err);
      } 
      window.location.href = window.location.href;
    }
    addToDb();
  }
  return <form className="form">
    {/* <h2 className="formH2">   ?</h2> */}
    <SplitText
      text="   ?"
      className="formH2"
      delay={100}
      duration={0.6}
      ease="power3.out"
      splitType="chars"
      from={{ opacity: 0, y: 40 }}
      to={{ opacity: 1, y: 0 }}
      threshold={0.1}
      rootMargin="-100px"
      textAlign="center"
    />
    <div className="formDiv">
      <input id="addTweetName" className="textInput" type="text" placeholder="..."/>
      <input id="addTweetText" className="textInput" type="text" placeholder="  ..."/>
    </div>
    <button onClick={addTweet} type="button" id="postBtn" className="submit"></button>
    <ClipLoader color="teal" id="mainPostLoader" className="invLoader"/>
  </form>
}

function Tweet({id, user, text, replies, date}){
  const[repliesElements, setRepliesElements] = useState();
  useEffect(() => {
    setRepliesElements(replies.map(e => {
      // const arr = e.split("#$#");
      return <div className="repliesContChild">
        {/* <h3 className="userName"><span style={{fontWeight:"normal"}}>user: </span>{arr[0]}</h3> */}
        <p className="tweetText">{e}</p>
      </div>
    }));
  }, []);
  let dateDate = (new Date(date.seconds * 1000).getDate());
  let dateMonth = (new Date(date.seconds * 1000).getMonth()) + 1;
  let dateYear = (new Date(date.seconds * 1000).getFullYear()) % 1000;
  let dateHours = (new Date(date.seconds * 1000).getHours());
  let dateMinutes = (new Date(date.seconds * 1000).getMinutes());
  let dateSeconds = (new Date(date.seconds * 1000).getSeconds());
  const addToDb = async () => {
    if((/^\s*$/).test(document.querySelector(`#id${id}`).value))
      return;
    document.querySelector("#load" + id).classList.remove("invLoader");
    document.querySelector("#btn" + id).disabled = true;
    const docRef = doc(db, "tweets", id);
    let arr = replies;
    arr.push(document.querySelector(`#id${id}`).value);
    await updateDoc(docRef, {
      replies:arr,
    });
    document.querySelector(`#id${id}`).value = "";
    window.location.href = window.location.href;
  }

  return <div className="tweetCont"> 
    <div className="tweet">
      <h3 className="userName"><span style={{fontWeight:"normal"}}>user: </span>{user} <time className="datePosted"> &#x2022; {dateDate + "." + dateMonth + "." + dateYear} &#x2022; {dateHours + ":" + dateMinutes}</time></h3>
      <p className="tweetText">{text}</p>
      <div className="replyCont">
        <input className="replyInput" id={"id" + id} type="text" placeholder="..."/>
        <button onClick={addToDb} id={"btn" + id} className="replyButton" role="button"></button>
      </div>
      <div className="loaderParent invLoader" id={"load" + id}>
        <ClipLoader color="teal"/>
      </div>
    </div>
    <div className="repliesCont">
      {repliesElements}
    </div>
  </div>
}

function Tweets(){
  const[tweets, setTweets] = useState();

  useEffect(() => {
    const fetchData = async () => {
      const querySnapshot = await getDocs(collection(db, "tweets"));
      const data = querySnapshot.docs.map(doc => ({id:doc.id, ...doc.data()}));
      data.sort((a, b) => new Date(b.date.seconds * 1000) - new Date(a.date.seconds * 1000));
      let arr = [];
      data.forEach(e => {
      arr.push(<Tweet id={e.id} user={e.user} text={e.text} replies={e.replies} date={e.date}/>);
      });
      document.querySelector("#waitDataLoader").classList.add("invLoader");
      setTweets(arr);
      document.querySelector("#footer").classList.remove("invFooter");
    }
    fetchData();
  }, [])

  return <div className="tweetsCont">
    {tweets}
    <div id="waitDataLoader">
      <ClipLoader color="teal"/>
    </div>
  </div>
}

function Main(){
  const[headerFont, setHeaderFont] = useState("h1");
  useEffect(() => {
    window.addEventListener("scroll", () => {
      if(window.scrollY == 0){
        document.querySelector("#headerText").classList.remove('scrolled');
        document.querySelector("#headerText").classList.add('unscrolled');
      }
      else{
        document.querySelector("#headerText").classList.remove('unscrolled');
        document.querySelector("#headerText").classList.add('scrolled');
      }
    })
  }, [])
  return <>
    <img src={require("./media/student-dorms-brutalist-architecture-macedonia-skopje.jpg")} className="imgBackground"/>
    <header class="header">
      <h1 id="headerText" className="unscrolled">   - </h1>
    </header>
    <div className="container">
      <Form/>
      <Tweets/>
    </div>
    <footer id="footer" className="invFooter">
      <h2>This is the end :/</h2>
    </footer>
  </>
}

export default Main;